import React from 'react'
import {Link} from "react-router-dom"
import "./Home.scss"
import Hero from './Hero';
import CategorySlider from '../../Custom/CategorySlider/CategorySlider' 
import pepsi from "../../Custom/Images/2.png"
import AdvertBanner from '../../Custom/AdvertBanner/AdvertBanner'
import Advert from "../../Custom/Images/Adverts.jpg"
import Benefits from '../../Custom/Benefits/Benefits';

export default function Home() {
    return (
        <div>
            <div className="promotions" >
                <span>Welcome to Geetico home of great deals </span>
            </div>
                <Hero/>
                <div className="benefitsWrapper" >
                    <Benefits/>
                </div>
                <h2 className="categoriesHeader" style={{"textAlign":"center","paddingBottom":"20px"}}>Categories</h2>
                <div className="categories" >
                    <Link to ="/Shop/TopSellers"><h4>Top Sellers</h4></Link>
                    <Link to ="/Shop"><h4>New Arrivals</h4></Link>
                    <Link to ="/Shop"><h4>Beer, Wine and Spirit</h4></Link>
                    <Link to ="/Shop"><h4>Beverages</h4></Link>
                    <Link to ="/Shop"><h4>Drinks</h4></Link>
                    <Link to ="/Shop"><h4>Cooking, Spices and Baking Ingredients</h4></Link>
                    <Link to ="/Shop"><h4>Food Cupboard and Breakfast Food</h4></Link>
                    <Link to ="/Shop"><h4>Biscuits, Candy and Chocolate</h4></Link>
                    <Link to ="/Shop"><h4>Jams, Canned and Packaged Condiments</h4></Link>
                    <Link to ="/Shop"><h4>Household Supplies</h4></Link>
                </div>
                <h2 style={{"textAlign":"center","paddingTop":"20px"}}>Popular Categories</h2>
                <div className="categorySliderWrapper showMobile" >
                    <CategorySlider pepsi={pepsi} slides={2} category ="Top Sellers" />
                    <CategorySlider pepsi={pepsi} slides={2} category ="New Arrivals"/>
                    <div className="advertBetween">
                    <img src={Advert} alt="advert"/>
                    </div>
                    <CategorySlider pepsi={pepsi} slides={2} category ="Office"/>
                    <CategorySlider pepsi={pepsi} slides={2} category ="Fashion"/>
                </div>
                <div className="categorySliderWrapper showWeb" >
                    <CategorySlider pepsi={pepsi} slides={5} category ="Top Sellers"/>
                    <CategorySlider pepsi={pepsi} slides={5} category ="New Arrivals"/>
                    <div className="advertBetween">
                    <img src={Advert} alt="advert"/>
                    </div>
                    <CategorySlider pepsi={pepsi} slides={5} category ="Office"/>
                    <CategorySlider pepsi={pepsi} slides={5} category ="Fashion"/>
                </div>
                <AdvertBanner />
            
        </div>
    )
}
