import React, { useState } from 'react'
import "./Benefits.scss"
import ItemsCarousel from 'react-items-carousel';
import cash from '../Images/cash.png'
import delivery from "../Images/delivery-truck.png"
import value from "../Images/diamond.png"

export default function Benefits() {
    const [activeItemIndex, setActiveItemIndex] = useState(0);
  const chevronWidth = 20;
    return (
        <div className="Benefits">
            <div className= "showMobile">
            <ItemsCarousel
            requestToChangeActive={setActiveItemIndex}
            activeItemIndex={activeItemIndex}
            numberOfCards={1}
            gutter={20}
            leftChevron={<p>{"<"}</p>}
            rightChevron={<p>{">"}</p>}
            outsideChevron
            chevronWidth={chevronWidth}
            slidesToScroll={1}
            >
                <div className="benefits">
                    <img src={cash} alt="Secure Payments"/>
                    <p>Secure Payment</p>
                </div>
                 <div className="benefits">
                    <img src={value} alt="Best Value"/>
                    <p>Best Value</p>
                </div>
                 <div className="benefits">
                    <img src={delivery} alt="Quick Delivery"/>
                    <p>Quick Delivery</p>
                </div>
            </ItemsCarousel>
            </div>
            <div>
            <div className="showWeb">
            <div className="benefitsContainer">
            <div className="benefits firstBenefit">
                    <img src={cash} alt="Secure Payments"/>
                    <p>Secure Payment</p>
                </div>
                 <div className="benefits secondBenefit">
                    <img src={value} alt="Best Value"/>
                    <p>Best Value</p>
                </div>
                 <div className="benefits">
                    <img src={delivery} alt="Quick Delivery"/>
                    <p>Quick Delivery</p>
                </div>
                </div>
                </div>
            </div>
        </div>
    )
}
