import React, { useState } from 'react';
import {Link} from "react-router-dom"
import ItemsCarousel from 'react-items-carousel';
import "./CategorySlider.scss"
import PublicItem from '../PublicItem/PublicItem';
import Card from '../Card/Card';


export default function CategorySlider({pepsi,category,slides}) {
    const item = {Description: "Roasted Almonds",
    Name: "Generic Placeholder",
    Price: 3,
    id: 6,
    image: ""}
    const [activeItemIndex, setActiveItemIndex] = useState(0);
  const chevronWidth = 20;
    return (
        <div style={{"padding":"20px"}}className="categorySliderContainer">
        <div  className="categorySlider">
        <div className="heading">
        <h2>{category}</h2>
        <p>View More...</p>
        </div>
      <ItemsCarousel
        requestToChangeActive={setActiveItemIndex}
        activeItemIndex={activeItemIndex}
        numberOfCards={slides}
        gutter={20}
        leftChevron={<p>{"<"}</p>}
        rightChevron={<p>{">"}</p>}
        outsideChevron
        chevronWidth={chevronWidth}
        slidesToScroll={2}
      >
                <div className="one"><div className="two"><Card key = {item.id} item = {item}/></div></div>
                <div className="one"><div className="two"><Card key = {item.id} item = {item}/></div></div>
                <div className="one"><div className="two"><Card key = {item.id} item = {item}/></div></div>
                <div className="one"><div className="two"><Card key = {item.id} item = {item}/></div></div>
                <div className="one"><div className="two"><Card key = {item.id} item = {item}/></div></div>
                <div className="one"><div className="two"><Card key = {item.id} item = {item}/></div></div>
      </ItemsCarousel>
    </div>
    </div>
    )
}
