import React, {useContext} from 'react'
import Pepsi from "../Images/2.png"
import "./Card.scss"
import {Context} from "../../Context"

export default function Card({item}) {
    const {Name, Price} = item
    console.log(item)
    const {total,reload} = useContext(Context) 
    const cart = JSON.parse(localStorage.getItem("cart"))
    //ADD TO CART
    const addToCart = ()=>{
        const existingCartItem = cart.find(content=> content.id === item.id )
        if(existingCartItem){
            cart.map(content=> {if(content.id === item.id){ content.quantity++; total.set(prev=>[...prev])} return(content)})
        }
        else{cart.push({...item, quantity:1})}
        localStorage.setItem("cart",JSON.stringify(cart))
    }
    // //INCREASE CART
    // const increase = ()=>{
    //     reload.set(prev=>[...prev])
    //     cart.map(content=>{if((content.id === item.id)){ content.quantity++; reload.set(prev=>[...prev])}})
    //     localStorage.setItem("cart",JSON.stringify(cart))
        
    //   }
    // //DECREASE CART
    //   const decrease = ()=>{
    //     cart.map(
    //       content=>{if((content.id === item.id)){ content.quantity-- ;reload.set(prev=>[...prev]);if(content.quantity<1){
    //         const newFilteredCart= cart.filter(newContent=>newContent.id !== item.id)
    //         content.quantity=0 
    //       }}})
    //     localStorage.setItem("cart",JSON.stringify(cart))
    //   }
    return (
        <div>
            <div className="publicItem">
                <img src= {Pepsi} alt="single item"/>
                <div className="item-details">
                    <h4>{Name}</h4>
                <div>
                <h3>N{Price}</h3>
                <p onClick={addToCart} className="addToCart">Buy</p>
                {/* <span onClick={decrease}>{"<"}</span><span onClick={increase}>{">"}</span> */}
                </div>
                </div>           
            </div>
        </div>
    )    
}
