import React from 'react'
import {Link} from "react-router-dom"
import "./PhoneMenu.scss"
import Button from '../Button/Button'

export default function PhoneMenu({close}) {
    return (
        <div className="phoneMenuWrapper">
        <div className="phoneMenu">
            <p onClick={close} className="closeMenu"> X  Close</p>
            <div className="phoneMenuAuth">
                <Button content="SignIn/SignUp" Buttontype="auth"/>
            </div>
            <div className="links">
            <Link to ="/"><h3>Home</h3></Link>
            <h3>Categories</h3>
            <div className="lineWrapper"><hr/></div>            
            <div className="phoneMenuCategories">
            <Link to ="/Shop/TopSellers"><h4>Top Sellers</h4></Link>
            <Link to ="/Shop"><h4>New Arrivals</h4></Link>
            <Link to ="/Shop"><h4>Beer, Wine and Spirit</h4></Link>
            <Link to ="/Shop"><h4>Beverages</h4></Link>
            <Link to ="/Shop"><h4>Drinks</h4></Link>
            <Link to ="/Shop"><h4>Cooking, Spices and Baking Ingredients</h4></Link>
            <Link to ="/Shop"><h4>Food Cupboard and Breakfast Food</h4></Link>
            <Link to ="/Shop"><h4>Biscuits, Candy and Chocolate</h4></Link>
            <Link to ="/Shop"><h4>Jams, Canned and Packaged Condiments</h4></Link>
            <Link to ="/Shop"><h4>Household Supplies</h4></Link>
            </div>
            <div className="socialMediaPages">
            <h4>Social Media Pages</h4>
            </div>
            </div>
            
        </div>
        </div>
    )
}
